/* H Tags */
h1,h2,h3,h4,h5,h6 {
	font-family: "le-monde-livre-std",serif;
	font-style: italic;
	font-weight: 300;
}

/* Hero Section */
.hero-wrap {
	img.hero {
		width:100%;
		position:absolute;
		top:100px;
		left:0;
	}
	.left-nav {
		position:relative;
		z-index:999;
		.phone {
			display:inline-block;
			span {
				font-family: "le-monde-livre-std",serif;
				font-style: italic;
				font-weight: 300;
			}
		}
	}
}
